/* 기본 스타일 */
body {
  font-family: Arial, Helvetica, sans-serif, serif;
  
  background-color: #f0f0f0;

  
}
tr{
  border: rgb(175, 175, 175) ;
}
.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.7s;
}

.main-bg {
  height: 500px;
  background-image: url('./image/bg.png');
  background-size: cover;
  background-position: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  
  


 
  
  
}
#container {
  padding-top: 20px;
}
.Brand {
  cursor: pointer;
  
  
}
.App {
  text-align: center;
  letter-spacing: -0.8px;
  height: 100%;
  

  
}
#me {
  border: 1px rgb(39, 38, 38)solid;
  



  
  
  height: 500px;
}
.mee {
  margin: 15%;
  margin-bottom: 0px;
  padding-bottom: 15%;
}
.introduce{
  float: left;
  width: 10%;
  height: 10%;
}
.introduce1{
  float: left;
  width: 10%;
  height: 10%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.col-md-4{
 
  /*다크 모드 때 변화*/

  margin: 0px;
}
thead {
  border-bottom: black 2px solid;
}
#Table-title{
  padding-top: 50px;
  padding-bottom: 30px;
}
#Table-site {
  margin: 0 auto;
  width: 60%;
  padding: 10px;
  margin-top: 50px;
  
  
  border: 1px ridge black ;
  border-radius: 0.5rem;
}
.table {
  height: 200px;
  
  border-radius: 0.5rem;
  
  
  

}
.me-auto{
  
  padding-left: 50%;
}
.bar-left{
  float: right;
}
.night-mode{
  height: 50px;
  width: 50px;
  background-color: 
  rgb(207, 215, 223);
  position: relative;
  border-radius: 50%;
  left: 90%;
  top: 90%;
  position: absolute;
  position: fixed;
  cursor: pointer;
  
  
  
  
}

 #moon{
  padding-right: 8px;
  margin: 0 auto;
  position: absolute;
  top: 20%;
  left: 15%;
} 
#image{
  padding-top: 10px;
}
.bar{
  position: fixed;
  top: 0; left: 0;
  width: 100%;
  background-color: #f0f0f0;
  border-bottom: #868686 1px inset;
}
.img {
  cursor: pointer;
}
.row{
  padding-top: 50px;
}
.col-md-6.mt-4{
  border: 1px ridge black ;
  border-radius: 0.5rem;
  width: 400px;
  margin-left: 40px;
}
.btn.btn-danger{
  margin-top: 30%;
}





